import { Component, OnInit } from '@angular/core';
import { ProductosService } from '../../services/productos.service'
import { Producto } from 'src/app/models/producto';
import { TableStructure } from 'src/app/own/data-table/tableStructure';
import { Observable } from 'rxjs';
import { NbDialogService } from '@nebular/theme';
import { DialogComponent } from 'src/app/own/dialog/dialog.component';

@Component({
  selector: 'app-productos',
  templateUrl: './productos.component.html',
  styleUrls: ['./productos.component.scss']
})

export class ProductosComponent implements OnInit {

  public tableStructure:TableStructure={
    columns:[
      {title:'ID',name:'id',editable:false,type:"number",'filter':false},
      {title:'Nombre',name:'nombre',editable:true,type:"text",'filter':true},
      {title:'C贸digo',name:'codigo_producto',editable:true,type:"number",'filter':true},
      {title:'Marca',name:'marca',editable:true,type:"text",'filter':true},
      {title:'Caducidad',name:'fecha_caducidad',editable:true,type:"date",'filter':true},
      {title:'Ubicaci贸n',name:'ubicacion',editable:true,type:"text",'filter':true},
      {title:'Medida',name:'unidad_medida',editable:true,type:"text",'filter':true},
      {title:'Presentaci贸n',name:'presentacion',editable:true,type:"text",'filter':true},
      {title:'Stock min',name:'stock_minimo',editable:true,type:"number",'filter':true},
      {title:'Stock max',name:'stock_maximo',editable:true,type:"number",'filter':true},
      {title:'Stock act',name:'stock_actual',editable:true,type:"number",'filter':true},
      {title:'Costo',name:'costo',editable:true,type:"number",'filter':true},
      {title:'Iva',name:'iva',editable:true,type:"number",'filter':true},
      {title:'Precio',name:'precio',editable:true,type:"number",'filter':true},
      {title:'Utilidad',name:'utilidad_bruta',editable:true,type:"number",'filter':true},
      {title:'Descuento',name:'descuento',editable:true,type:"number",'filter':true},
      {title:'Comisi贸n',name:'comision',editable:true,type:"number",'filter':true},
      {title:'Imagen',name:'imagen',editable:true,type:"file",'filter':false},
      {title:'Descripci贸n',name:'descripcion',editable:true,type:"textarea",'filter':true},

    ],
    buttons: {
      create: true,
      delete: true,
      update: true
    }
  }
  public productos : Observable<Producto[]>;
  public errorsPerInput=[];
  public messageSuccess="";

  constructor(private productoService: ProductosService,private dialogService: NbDialogService) { }

  ngOnInit() {
   this.productos=this.productoService.getProductos();
  }
  public create(newData,dataTable){
    this.reset();
    dataTable.charginData++;
    this.productoService.createProducto(newData)
      .subscribe(productoResponse=>{
        dataTable.charginData--;
        this.messageSuccess="Producto creado exitosamente";
        dataTable.pushData(productoResponse);
      },error=>this.displayErrors(error,dataTable));
  }
  public update(updateData,dataTable){
    this.reset();
    dataTable.charginData++;
    this.productoService.updateProducto(updateData)
      .subscribe(productoResponse => {
        dataTable.charginData--;
        dataTable.updateData(productoResponse);
        this.messageSuccess = "Producto actualizado correctamente";
      }, error => this.displayErrors(error));
  }
  public delete(deleteData, dataTable) {
    this.dialogService.open(DialogComponent, {
      context: {
        title: '隆Cuidado!',
        content: `Si elimina este registro, se perder谩 el historial de movimientos relacionados al registro de ${(deleteData.codigo_producto+"").toUpperCase()}. 驴Desea continuar?`
      },
    }).onClose.subscribe(response => {
      if (response) {
        this.reset();
        dataTable.charginData++;

        this.productoService.deleteProducto(deleteData).subscribe(response => {
          dataTable.charginData--;
          this.messageSuccess = "Producto eliminado correctamente"
          dataTable.deleteData(response);
        }, error => this.displayErrors(error));
      }
    });
  }

  private reset() {
    this.errorsPerInput = [];
    this.messageSuccess = "";
    setTimeout(() => {
      this.errorsPerInput = [];
      this.messageSuccess = "";
    }, 10000);
  }
  private displayErrors(error,dataTable?) {
    dataTable? dataTable.charginData--:0;
    for (const key in error.error.errors) {
      this.errorsPerInput.push(error.error.errors[key]);
    }
  }

}
