import {Injectable} from '@angular/core';
import {Producto} from '../models/producto';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})

export class ProductosService {

  constructor(private http: HttpClient) {
  }

  public getProductos(): Observable<Producto[]> {
    return this.http.get<Producto[]>('http://api.vidamarinapuebla.com/api/productos');
  }

  public updateProducto(producto: Producto): Observable<Producto> {
    return this.http.put<Producto>('http://api.vidamarinapuebla.com/api/productos/' + producto.id, producto);
  }

  public createProducto(producto: Producto): Observable<Producto> {
    return this.http.post<Producto>('http://api.vidamarinapuebla.com/api/productos', producto);
  }

  public deleteProducto(producto: Producto): Observable<Producto> {
    return this.http.delete<Producto>('http://api.vidamarinapuebla.com/api/productos/' + producto.id);
  }

}
